.search-container {
  display: flex;
  justify-content: center;
  margin: 10px;
}

.search-input {
  padding: 12px 17px;
  margin-left: 180px;
  font-size: 15px;
  width: 450px;
  border-width: 0;
  background: #a592f2;
  color: #e7e9f0;
  border-radius: 50px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.search-input::placeholder {
  color: #e7e9f0;
}

.search-input:focus {
  outline: none;
}